*{
  font-family: 'Archivo Narrow', sans-serif;
}
.sushiSanKun-img{
  height: 210px;
  
}
.button-text{
  font-size: 49px;
  margin-bottom: auto;
}

.withoutPadding{
  padding: 0%;
  box-shadow: 0 0.3rem 0.8rem rgba(0,0,0,0.9);;
}

.cardShadow {
  box-shadow: 0 0.1rem 0.4rem rgba(0,0,0,0.3);
}

.float-h3{
  float: right;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.startPage{
  background-image: url("./assets/fondoPrimeraPagina.png");
}

.home{
margin-top: 2%;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}

